@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@700&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@700&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@700&family=Open+Sans:wght@300&display=swap);
.screen {
  display: flex;
}

.sidebar {
  flex: 1 1;
}

.pages {
  flex: 2 1;
}

body {
  margin: 0;
  padding: 0;
}

.sidebar-container {
  width: 20%;
  height: 100vh;
  background-color: rgb(60, 90, 90);
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.sidebar-container img {
  width: 80%;
  height: 80%;
  border-radius: 50%;
}

.directory a {
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-family: 'Open Sans';
  font-weight: 600;
}

.aboutMe-container {
  width: 80%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  position: right;
  justify-content: center;
  align-items: left;
  text-align: left;
}

h1 {
  font-size: 4.5rem;
  font-family: 'Antonio';
  line-height: 3rem;
  margin-bottom: 0;
}

h2 {
  font-family: 'Open Sans';
}

.info {
  font-family: 'Open Sans';
  font-size: 22px;
}


.projects-container {
  width: 80%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  position: right;
  justify-content: top;
  align-items: left;
  text-align: left;
}

h3 {
  font-size: 3.5rem;
  font-family: 'Antonio';
  line-height: 0rem;
}

.info {
  font-family: 'Open Sans';
  font-size: 18px;
}
.education-container {
  width: 80%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  position: right;
  justify-content: top;
  align-items: left;
  text-align: left;
}

.info {
  font-family: 'Open Sans';
  font-size: 22px;
  margin-top: 0px;
}

h2 {
  margin-bottom: 3px;
  font-family: 'Open Sans';
}
.experience-container {
  width: 80%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  position: right;
  justify-content: top;
  align-items: left;
  text-align: left;
}
