@import url('https://fonts.googleapis.com/css2?family=Antonio:wght@700&family=Open+Sans:wght@300&display=swap');

body {
  margin: 0;
  padding: 0;
}

.sidebar-container {
  width: 20%;
  height: 100vh;
  background-color: rgb(60, 90, 90);
  display: flex;
  flex-direction: column;

  position: sticky;
  top: 0;

  justify-content: center;
  align-items: center;
  text-align: center;
}

.sidebar-container img {
  width: 80%;
  height: 80%;
  border-radius: 50%;
}

.directory a {
  display: flex;
  flex-direction: column;
  padding-top: 50px;

  text-decoration: none;
  color: white;
  font-size: 18px;
  font-family: 'Open Sans';
  font-weight: 600;
}
